Object.defineProperty(e,"__esModule",{value:!0}),e.DataGioiThieu=void 0;var n=[{id:1,tieude:"\u1ee8ng d\u1ee5ng mobile: Tra c\u1ee9u th\xf4ng tin quy ho\u1ea1ch x\xe2y d\u1ef1ng th\xe0nh ph\u1ed1 Bi\xean H\xf2a",img:r(d[0]),createdate:" 13/09/2021",dessort:"\u0110\u1ec3 chu\u1ea9n b\u1ecb cho T\u1ebft Nguy\xean \u0111\xe1n T\xe2n S\u1eedu 2021, TP.Bi\xean H\xf2a v\xe0 TP.Long Kh\xe1nh \u0111ang tri\u1ec3n khai ch\u1ec9nh trang \u0111\xf4 th\u1ecb, ch\u1ee7 y\u1ebfu l\xe0 trang tr\xed \u0111\u01b0\u1eddng ph\u1ed1, c\u1ee7ng c\u1ed1 h\u1ec7 th\u1ed1ng t\xedn hi\u1ec7u tr\xean \u0111\u01b0\u1eddng, c\u1ea3i t\u1ea1o h\u1ea1 t\u1ea7ng giao th\xf4ng n\u1ed9i th\xe0nh",description:"<img alt='' src='http://quanlydothi.bienhoa.dongnai.gov.vn/FileUpload/images/QHXD_BH.jpg' style='width: 600px; height: 400px;'></p><p style='box-sizing: inherit; margin: 10px auto 0px; color: rgba(0, 0, 0, 0.87); font-family: arial; font-size: 15px; text-align: center;'><span style='font-size:14px;'><em><span style='color: rgb(0, 68, 136);'></span><strong style='box-sizing: inherit; color: rgb(0, 68, 136); background-color: rgb(233, 233, 233);'/><p style='margin: 0px; line-height: 1.6; padding: 0px 3px; color: rgb(51, 51, 51); font-family: Tahoma; font-size: 15px;'>\u0110\u1ec3 s\u1eed d\u1ee5ng \u1ee9ng d\u1ee5ng n\xe0y, ng\u01b0\u1eddi d\xf9ng t\u1ea3i mi\u1ec5n ph\xed ph\u1ea7n m\u1ec1m QHXD.BH tr\xean c\xe1c ch\u1ee3 \u1ee9ng d\u1ee5ng nh\u01b0: App Store \u0111\u1ed1i v\u1edbi thi\u1ebft b\u1ecb c\xf3 h\u1ec7 \u0111i\u1ec1u h\xe0nh IOS nh\u01b0: iPhone, iPad, Macbook lo\u1ea1i ch\u1ea1y vi x\u1eed l\xfd Silicon c\u1ee7a Apple; CH Play \u0111\u1ed1i v\u1edbi c\xe1c thi\u1ebft b\u1ecb ch\u1ea1y h\u1ec7 \u0111i\u1ec1u h\xe0nh Android.</p>"},{id:2,tieude:"Ch\u1ec9nh trang \u0111\xf4 th\u1ecb \u0111\xf3n n\u0103m m\u1edbi",img:r(d[1]),createdate:" 01/09/2021",dessort:"\u0110\u1ec3 chu\u1ea9n b\u1ecb cho T\u1ebft Nguy\xean \u0111\xe1n T\xe2n S\u1eedu 2021, TP.Bi\xean H\xf2a v\xe0 TP.Long Kh\xe1nh \u0111ang tri\u1ec3n khai ch\u1ec9nh trang \u0111\xf4 th\u1ecb, ch\u1ee7 y\u1ebfu l\xe0 trang tr\xed \u0111\u01b0\u1eddng ph\u1ed1, c\u1ee7ng c\u1ed1 h\u1ec7 th\u1ed1ng t\xedn hi\u1ec7u tr\xean \u0111\u01b0\u1eddng, c\u1ea3i t\u1ea1o h\u1ea1 t\u1ea7ng giao th\xf4ng n\u1ed9i th\xe0nh",description:"<span id='ContentPlaceHolder1_lblNoiDung'><p style='box-sizing: inherit; margin: 10px auto 0px; color: rgba(0, 0, 0, 0.87); font-family: arial; font-size: 15px;'>N\u0103m nay, vi\u1ec7c ch\u1ec9nh trang \u0111\xf4 th\u1ecb c\u1ee7a TP.Bi\xean H\xf2a v\xe0 TP.Long Kh\xe1nh \u0111\u01b0\u1ee3c c\xe1c c\u01a1 quan ch\u1ee9c n\u0103ng th\u1ef1c hi\u1ec7n s\u1edbm h\u01a1n so v\u1edbi m\u1ecdi n\u0103m \u0111\u1ec3 \u0111\u1ea3m b\u1ea3o ti\u1ebfn \u0111\u1ed9 v\xe0 ch\u1ea5t l\u01b0\u1ee3ng thi c\xf4ng.</p><p style='box-sizing: inherit; margin: 10px auto 0px; color: rgba(0, 0, 0, 0.87); font-family: arial; font-size: 15px; text-align: center;'><img alt='' src='http://quanlydothi.bienhoa.dongnai.gov.vn/FileUpload/images/images2339717_t13_1.jpg' style='width: 600px; height: 400px;'></p><p style='box-sizing: inherit; margin: 10px auto 0px; color: rgba(0, 0, 0, 0.87); font-family: arial; font-size: 15px; text-align: center;'><span style='font-size:14px;'><em><span style='color: rgb(0, 68, 136);'>D\u1ea3i ph\xe2n c\xe1ch m\u1ec1m tr\xean \u0111\u01b0\u1eddng Nguy\u1ec5n \xc1i Qu\u1ed1c (TP.Bi\xean H\xf2a) \u0111\u01b0\u1ee3c ch\u1ec9nh s\u1eeda, s\u01a1n m\u1edbi, \u0111\u1ea3m b\u1ea3o m\u1ef9 quan \u0111\xf4 th\u1ecb v\xe0 an to\xe0n cho c\xe1c ph\u01b0\u01a1ng ti\u1ec7n l\u01b0u th\xf4ng.&nbsp;</span><strong style='box-sizing: inherit; color: rgb(0, 68, 136); background-color: rgb(233, 233, 233);'>\u1ea2nh: \u0110\u0103ng T\xf9ng</strong></em></span></p><p style='box-sizing: inherit; margin: 10px auto 0px; color: rgba(0, 0, 0, 0.87); font-family: arial; font-size: 15px;'><strong style='box-sizing: inherit;'>*&nbsp;</strong><strong style='box-sizing: inherit;'>T\xedch c\u1ef1c l\xe0m \u0111\u1eb9p \u0111\u01b0\u1eddng ph\u1ed1</strong></p><p style='box-sizing: inherit; margin: 10px auto 0px; color: rgba(0, 0, 0, 0.87); font-family: arial; font-size: 15px;'>T\u1ea1i TP.Bi\xean H\xf2a, ngay t\u1eeb th\xe1ng 12-2020, Ph\xf2ng Qu\u1ea3n l\xfd \u0111\xf4 th\u1ecb \u0111\xe3 cho th\u1ea3m nh\u1ef1a l\u1ea1i m\u1ed9t s\u1ed1 \u0111o\u1ea1n tr\xean c\xe1c \u0111\u01b0\u1eddng l\u1edbn nh\u01b0: Nguy\u1ec5n \xc1i Qu\u1ed1c, H\xe0 Huy Gi\xe1p, Ph\u1ea1m V\u0103n Thu\u1eadn, Tr\u1ea7n Qu\u1ed1c To\u1ea3n... H\u1ec7 th\u1ed1ng v\u1ea1ch k\u1ebb tr\xean \u0111\u01b0\u1eddng, bi\u1ec3n b\xe1o, d\u1ea3i ph\xe2n c\xe1ch, \u0111\xe8n chi\u1ebfu s\xe1ng c\u0169ng \u0111\u01b0\u1ee3c l\xe0m m\u1edbi, ch\u1ec9nh s\u1eeda, gi\xfap ng\u01b0\u1eddi d\xe2n tham gia giao th\xf4ng an to\xe0n h\u01a1n.</p><p style='box-sizing: inherit; margin: 10px auto 0px; color: rgba(0, 0, 0, 0.87); font-family: arial; font-size: 15px;'>\u0110\xe1ng ch\xfa \xfd, c\xe1c c\u1ed5ng ch\xe0o c\u1ee7a TP.Bi\xean H\xf2a tr\xean \u0111\u01b0\u1eddng 30-4, \u0111\u01b0\u1eddng Ph\u1ea1m V\u0103n Thu\u1eadn, ng\xe3 n\u0103m Bi\xean H\xf9ng c\u0169ng \u0111\u01b0\u1ee3c ch\u1ec9nh s\u1eeda, thay \u0111\u1ed5i v\u1eadt li\u1ec7u, l\xe0m m\u1edbi l\u1ea1i. C\xe1c c\xf4ng vi\xean l\u1edbn \u0111\u01b0\u1ee3c trang tr\xed l\u1ea1i, b\u1ed5 sung th\xeam c\xe1c ti\u1ec3u c\u1ea3nh \u0111\u1ec3 ph\u1ee5c v\u1ee5 nhu c\u1ea7u tham quan, vui ch\u01a1i c\u1ee7a ng\u01b0\u1eddi d\xe2n trong d\u1ecbp T\u1ebft D\u01b0\u01a1ng l\u1ecbch c\u0169ng nh\u01b0 T\u1ebft Nguy\xean \u0111\xe1n s\u1eafp t\u1edbi.</p><p style='box-sizing: inherit; margin: 10px auto 0px; color: rgba(0, 0, 0, 0.87); font-family: arial; font-size: 15px;'>Tr\u01b0\u1edfng ph\xf2ng Qu\u1ea3n l\xfd \u0111\xf4 th\u1ecb TP.Bi\xean H\xf2a Tr\u01b0\u01a1ng V\u0129nh Hi\u1ec7p cho bi\u1ebft: 'Ngay t\u1eeb cu\u1ed1i n\u0103m 2020, ch\xfang t\xf4i \u0111\xe3 c\xf3 v\u0103n b\u1ea3n tham m\u01b0u cho UBND TP.Bi\xean H\xf2a \u0111\u1ec3 ch\u1ec9nh trang l\u1ea1i \u0111\xf4 th\u1ecb h\u01b0\u1edbng t\u1edbi \u0111\u1ea3m b\u1ea3o an to\xe0n cho ng\u01b0\u1eddi d\xe2n, m\u1ef9 quan cho th\xe0nh ph\u1ed1 trong n\u0103m m\u1edbi. \u0110\u1ebfn nay, c\xf4ng vi\u1ec7c ch\u1ec9nh trang \u0111\xf4 th\u1ecb c\u01a1 b\u1ea3n ho\xe0n t\u1ea5t, c\xe1c tuy\u1ebfn \u0111\u01b0\u1eddng c\u0169ng \u0111\u01b0\u1ee3c l\u1eafp \u0111\xe8n trang tr\xed, c\xe1c c\xf4ng vi\xean \u0111\xe3 ho\xe0n thi\u1ec7n c\xe1c ti\u1ec3u c\u1ea3nh \u0111\xf3n ch\xe0o n\u0103m m\u1edbi'.</p><p style='box-sizing: inherit; margin: 10px auto 0px; color: rgba(0, 0, 0, 0.87); font-family: arial; font-size: 15px;'>C\xf2n t\u1ea1i TP.Long Kh\xe1nh, b\xean c\u1ea1nh trang tr\xed, ch\u1ec9nh trang \u0111\u01b0\u1eddng ph\u1ed1 t\u1ea1i c\xe1c tr\u1ee5c \u0111\u01b0\u1eddng ch\xednh, \u0111\u01b0\u1eddng l\u1edbn nh\u01b0: H\u1ed3 Th\u1ecb H\u01b0\u01a1ng, H\xf9ng V\u01b0\u01a1ng..., n\u0103m nay, th\xe0nh ph\u1ed1 s\u1ebd m\u1edf cao \u0111i\u1ec3m tu\u1ea7n tra x\u1eed l\xfd c\xe1c vi ph\u1ea1m h\xe0nh ch\xednh t\u1ea1i c\xe1c \u0111i\u1ec3m c\xf4ng c\u1ed9ng, ch\u1ee7 y\u1ebfu l\xe0 v\u1ec1 tr\u1eadt t\u1ef1 an to\xe0n giao th\xf4ng, l\u1ea5n chi\u1ebfm v\u1ec9a h\xe8 v\xe0 ki\u1ec3m tra \u0111i\u1ec1u ki\u1ec7n an to\xe0n c\xe1c c\xf4ng tr\xecnh h\u1ea1 t\u1ea7ng giao th\xf4ng \u0111ang thi c\xf4ng.</p><p style='box-sizing: inherit; margin: 10px auto 0px; color: rgba(0, 0, 0, 0.87); font-family: arial; font-size: 15px;'>Ph\xf3 tr\u01b0\u1edfng ph\xf2ng Qu\u1ea3n l\xfd \u0111\xf4 th\u1ecb TP.Long Kh\xe1nh \u0110\u1eb7ng Nam Ph\u01b0\u1edbc cho hay: 'N\u0103m nay do \u1ea3nh h\u01b0\u1edfng c\u1ee7a d\u1ecbch Covid-19 n\xean vi\u1ec7c trang tr\xed \u0111\u01b0\u1eddng ph\u1ed1 s\u1ebd theo h\u01b0\u1edbng ti\u1ebft ki\u1ec7m nh\u01b0ng v\u1eabn \u0111\u1ea3m b\u1ea3o m\u1ef9 quan \u0111\xf4 th\u1ecb, an to\xe0n giao th\xf4ng, t\u1ea1o kh\xf4ng kh\xed vui xu\xe2n. T\xednh \u0111\u1ebfn th\u1eddi \u0111i\u1ec3m hi\u1ec7n t\u1ea1i, c\xf4ng t\xe1c ch\u1ec9nh trang \u0111\xf4 th\u1ecb \u1edf \u0111\u1ecba ph\u01b0\u01a1ng \u0111\xe3 c\u01a1 b\u1ea3n xong, ch\u1ec9 c\xf2n ch\u1edd c\u1eadn T\u1ebft \u0111\u1ec3 trang tr\xed hoa v\xe0 ti\u1ec3u c\u1ea3nh tr\xean c\xe1c \u0111\u01b0\u1eddng trung t\xe2m'.</p><p style='box-sizing: inherit; margin: 10px auto 0px; color: rgba(0, 0, 0, 0.87); font-family: arial; font-size: 15px;'><strong style='box-sizing: inherit;'>* Ch\xfa tr\u1ecdng h\u01a1n n\u1eefa \u0111\u1ebfn h\u1ea1 t\u1ea7ng giao th\xf4ng</strong></p><p style='box-sizing: inherit; margin: 10px auto 0px; color: rgba(0, 0, 0, 0.87); font-family: arial; font-size: 15px;'>Kh\xf4ng ch\u1ec9 ch\u1ec9nh trang \u0111\xf4 th\u1ecb v\xe0o d\u1ecbp n\u0103m m\u1edbi theo k\u1ebf ho\u1ea1ch m\xe0 th\u1eddi gian qua, c\u01a1 quan ch\u1ee9c n\u0103ng \u0111\xe3 c\xf3 nhi\u1ec1u ho\u1ea1t \u0111\u1ed9ng n\xe2ng c\u1ea5p, kh\u1eafc ph\u1ee5c c\xe1c h\u1ea1ng m\u1ee5c h\u1ea1 t\u1ea7ng giao th\xf4ng, c\u1ea3nh quan trong n\u1ed9i th\xe0nh t\u1ea1i TP.Bi\xean H\xf2a v\xe0 TP.Long Kh\xe1nh. \u0110\u1eb7c bi\u1ec7t nh\u1eefng v\u1ecb tr\xed h\u01b0 h\u1ecfng, b\u1ea5t h\u1ee3p l\xfd, \u1ea3nh h\u01b0\u1edfng tr\u1ef1c ti\u1ebfp \u0111\u1eddi s\u1ed1ng ng\u01b0\u1eddi d\xe2n nhanh ch\xf3ng \u0111\u01b0\u1ee3c c\xe1c c\u01a1 quan ch\u1ee9c n\u0103ng kh\u1eafc ph\u1ee5c.</p><p style='box-sizing: inherit; margin: 10px auto 0px; color: rgba(0, 0, 0, 0.87); font-family: arial; font-size: 15px;'>C\u1ee5 th\u1ec3 nh\u01b0, ngay sau khi B\xe1o&nbsp;<em style='box-sizing: inherit;'>\u0110\u1ed3ng Nai</em>&nbsp;c\xf3 b\xe0i ph\u1ea3n \xe1nh c\u1ea7n gi\u1edbi h\u1ea1n xe c\xf3 t\u1ea3i tr\u1ecdng l\u1edbn l\u01b0u th\xf4ng v\xe0o gi\u1edd cao \u0111i\u1ec3m tr\xean \u0111\u01b0\u1eddng B\xf9i H\u1eefu Ngh\u0129a (TP.Bi\xean H\xf2a),&nbsp; l\xe3nh \u0111\u1ea1o TP.Bi\xean H\xf2a \u0111\xe3 c\xf3 ki\u1ebfn ngh\u1ecb \u0111\u1ebfn ng\xe0nh ch\u1ee9c n\u0103ng c\u1ee7a t\u1ec9nh \u0111\u1ec1 ngh\u1ecb g\u1eafn bi\u1ec3n c\u1ea5m xe t\u1ea3i theo gi\u1edd tr\xean tuy\u1ebfn \u0111\u01b0\u1eddng n\xe0y. \u0110\u1ebfn gi\u1eefa th\xe1ng 12-2020, Ban An to\xe0n giao th\xf4ng t\u1ec9nh \u0111\xe3 l\u1eafp \u0111\u1eb7t bi\u1ec3n b\xe1o theo \u0111\u1ec1 xu\u1ea5t c\u1ee7a UBND th\xe0nh ph\u1ed1. Hay m\u1edbi \u0111\xe2y, \u0111\u01b0\u1eddng li\xean t\u1ed5 16A, 16B, KP.Ru\u1ed9ng H\u1eddi (P.B\u1ea3o Vinh, TP.Long Kh\xe1nh) \u0111\xe3 \u0111\u01b0\u1ee3c \u0111\u1ed5 b\xea t\xf4ng (r\u1ed9ng 3,5m, d\xe0i kho\u1ea3ng 1km) \u0111\u01b0a v\xe0o s\u1eed d\u1ee5ng, \u0111\u1ea3m b\u1ea3o an to\xe0n giao th\xf4ng cho ng\u01b0\u1eddi d\xe2n...</p><p style='box-sizing: inherit; margin: 10px auto 0px; color: rgba(0, 0, 0, 0.87); font-family: arial; font-size: 15px;'>\xd4ng Nguy\u1ec5n Minh M\u1eabn (ng\u1ee5 P.Su\u1ed1i Tre, TP.Long Kh\xe1nh) \u0111\xe1nh gi\xe1: 'N\u0103m nay, TP.Long Kh\xe1nh \u0111\xe3 c\xf3 nhi\u1ec1u thay \u0111\u1ed5i v\u1ec1 h\u1ea1 t\u1ea7ng giao th\xf4ng, nhi\u1ec1u tuy\u1ebfn \u0111\u01b0\u1eddng \u0111\u01b0\u1ee3c m\u1edf r\u1ed9ng, n\xe2ng c\u1ea5p. Tuy nhi\xean, t\xf4i c\u0169ng \u0111\u1ec1 ngh\u1ecb c\u1ea7n ph\u1ea3i xem x\xe9t ch\u1ec9nh s\u1eeda l\u1ea1i h\u1ec7 th\u1ed1ng v\u1ea1ch ph\xe2n l\xe0n t\u1ea1i khu v\u1ef1c B\u1ec7nh vi\u1ec7n \u0110a khoa khu v\u1ef1c Long Kh\xe1nh cho ph\xf9 h\u1ee3p. V\xec \u0111\xe2y l\xe0 m\u1ed9t b\u1ec7nh vi\u1ec7n l\u1edbn, n\u1eb1m tr\xean qu\u1ed1c l\u1ed9 1 nh\u01b0ng c\xe1c v\u1ea1ch ph\xe2n l\xe0n c\u0169 v\xe0 m\u1edbi ch\u1ed3ng l\u1ea5n, khi\u1ebfn c\xe1c ph\u01b0\u01a1ng ti\u1ec7n giao th\xf4ng g\u1eb7p l\xfang t\xfang, d\u1ec5 nguy hi\u1ec3m'.</p><p style='box-sizing: inherit; margin: 10px auto 0px; color: rgba(0, 0, 0, 0.87); font-family: arial; font-size: 15px;'>B\xean c\u1ea1nh \u0111\xf3, m\u1ed9t s\u1ed1 ng\u01b0\u1eddi d\xe2n \u1edf TP.Bi\xean H\xf2a c\u0169ng ph\u1ea3n \u1ea3nh, nhi\u1ec1u \u0111o\u1ea1n \u0111\u01b0\u1eddng n\u1ed9i th\xe0nh c\u1ea7n ch\xfa \xfd h\u01a1n trong qu\xe1 tr\xecnh thi c\xf4ng, nh\u1ea5t l\xe0 v\u1ea5n \u0111\u1ec1 ti\u1ebfn \u0111\u1ed9, b\u1ee5i v\xe0 ti\u1ebfng \u1ed3n c\xf4ng tr\u01b0\u1eddng. \u0110\u1eb7c bi\u1ec7t v\u1edbi c\xe1c c\xf4ng tr\xecnh ch\u01b0a k\u1ecbp ti\u1ebfn \u0111\u1ed9 tr\u01b0\u1edbc T\u1ebft Nguy\xean \u0111\xe1n 2021, c\u1ea7n ph\u1ea3i c\xf3 bi\u1ec7n ph\xe1p t\u1ea1m th\u1eddi \u0111\u1ec3 \u0111\u1ea3m b\u1ea3o an to\xe0n giao th\xf4ng cho ng\u01b0\u1eddi d\xe2n \u0111i l\u1ea1i.</p><p style='box-sizing: inherit; margin: 10px auto 0px; color: rgba(0, 0, 0, 0.87); font-family: arial; font-size: 15px;'>Ch\u1ecb Tr\u1ea7n Thanh Th\u1ea3o (ng\u1ee5 P.Th\u1ed1ng Nh\u1ea5t, TP.Bi\xean H\xf2a) \u0111\u1ec1 ngh\u1ecb th\xeam: 'Nhi\u1ec1u \u0111o\u1ea1n \u0111\u01b0\u1eddng n\u1ed9i \xf4 TP.Bi\xean H\xf2a \u0111\xe3 \u0111\u01b0\u1ee3c ch\u1ec9nh trang, l\xe0m \u0111\u1eb9p, k\u1ebb v\u1ea1ch ph\xe2n l\xe0n r\u1ea5t thu\u1eadn l\u1ee3i cho vi\u1ec7c di chuy\u1ec3n. Tuy nhi\xean, c\u01a1 quan ch\u1ee9c n\u0103ng c\u0169ng n\xean l\u01b0u \xfd ch\u1ec9nh s\u1eeda \u0111\u1ebfn m\u1ed9t s\u1ed1 \u0111o\u1ea1n v\u1ec9a h\xe8 \u0111\xe3 h\u01b0 h\u1ecfng, bong tr\xf3c nhi\u1ec1u nh\u01b0: \u0111\u01b0\u1eddng Nguy\u1ec5n \xc1i Qu\u1ed1c \u0111o\u1ea1n t\u1eeb ng\xe3 t\u01b0 T\xe2n Phong l\xean c\xf4ng vi\xean 30-4, su\u1ed1t tuy\u1ebfn \u0111\u01b0\u1eddng Ph\u1ea1m V\u0103n Thu\u1eadn... \u0111\u1ec3 \u0111\u1ea3m b\u1ea3o ng\u01b0\u1eddi \u0111i b\u1ed9 kh\xf4ng ph\u1ea3i \u0111i xu\u1ed1ng l\xf2ng \u0111\u01b0\u1eddng, m\u1ea5t an to\xe0n giao th\xf4ng'.</p><div>&nbsp;</div></span>"},{id:3,tieude:"\u0110\u1ea3m b\u1ea3o ph\xf2ng d\u1ecbch trong v\u1eadn chuy\u1ec3n h\xe0ng h\xf3a, c\xf4ng nh\xe2n, chuy\xean gia ",img:r(d[2]),createdate:" 01/09/2021",dessort:"\u0110\u1ec3 chu\u1ea9n b\u1ecb cho T\u1ebft Nguy\xean \u0111\xe1n T\xe2n S\u1eedu 2021, TP.Bi\xean H\xf2a v\xe0 TP.Long Kh\xe1nh \u0111ang tri\u1ec3n khai ch\u1ec9nh trang \u0111\xf4 th\u1ecb, ch\u1ee7 y\u1ebfu l\xe0 trang tr\xed \u0111\u01b0\u1eddng ph\u1ed1, c\u1ee7ng c\u1ed1 h\u1ec7 th\u1ed1ng t\xedn hi\u1ec7u tr\xean \u0111\u01b0\u1eddng, c\u1ea3i t\u1ea1o h\u1ea1 t\u1ea7ng giao th\xf4ng n\u1ed9i th\xe0nh",description:"<span id='ContentPlaceHolder1_lblNoiDung'><p style='color: rgb(0, 0, 0); font-family: arial; font-size: 13px; word-spacing: -0.1px; text-align: center;'>\n        <img alt='' src='http://quanlydothi.bienhoa.dongnai.gov.vn/FileUpload/images/images2396508_3taicho.jpg' style='width: 600px; height: 399px;'></p>\n    <p style='color: rgb(0, 0, 0); font-family: arial; font-size: 13px; word-spacing: -0.1px; text-align: center;'>\n        <em><span style='color: rgb(0, 68, 136); word-spacing: -0.1px;'>L\u1ef1c l\u01b0\u1ee3ng ch\u1ee9c n\u0103ng ki\u1ec3m tra y t\u1ebf \u0111\u1ed1i v\u1edbi ph\u01b0\u01a1ng ti\u1ec7n d\xe0nh cho c\xf4ng nh\xe2n, chuy\xean gia lao \u0111\u1ed9ng</span></em></p>\n    <p style='color: rgb(0, 0, 0); font-family: arial; font-size: 13px; word-spacing: -0.1px;'>\n        Nh\u1eb1m \u0111\u1ea3m b\u1ea3o c\xf4ng t\xe1c ph\xf2ng, ch\u1ed1ng d\u1ecbch Covid-19 trong ho\u1ea1t \u0111\u1ed9ng v\u1eadn chuy\u1ec3n h\xe0ng h\xf3a, v\u1eadn chuy\u1ec3n c\xf4ng nh\xe2n, chuy\xean gia t\u1ea1i c\xe1c doanh nghi\u1ec7p, c\u01a1 s\u1edf s\u1ea3n xu\u1ea5t kinh doanh th\u1ef1c hi\u1ec7n ph\u01b0\u01a1ng \xe1n \u201c3 t\u1ea1i ch\u1ed7\u201d, \u201c1 cung \u0111\u01b0\u1eddng, 2 \u0111\u1ecba \u0111i\u1ec3m\u201d ho\u1eb7c linh \u0111\u1ed9ng c\xf9ng l\xfac 2 ph\u01b0\u01a1ng \xe1n tr\xean, S\u1edf GT-VT y\xeau c\u1ea7u, l\xe1i xe, nh\xe2n vi\xean nghi\u1ec7p v\u1ee5 \u0111i c\xf9ng tr\xean ph\u01b0\u01a1ng ti\u1ec7n v\u1eadn chuy\u1ec3n h\xe0ng ho\xe1 ph\u1ea3i th\u1ef1c hi\u1ec7n nghi\xeam c\xe1c bi\u1ec7n ph\xe1p ph\xf2ng, ch\u1ed1ng d\u1ecbch trong su\u1ed1t qu\xe1 tr\xecnh v\u1eadn t\u1ea3i theo quy \u0111\u1ecbnh; mang theo b\u1ea3n ch\xednh gi\u1ea5y x\xe9t nghi\u1ec7m \xe2m t\xednh SARS-CoV-2 \u0111\u01b0\u1ee3c th\u1ef1c hi\u1ec7n trong v\xf2ng 3 ng\xe0y (72 gi\u1edd k\u1ec3 t\u1eeb khi c\xf3 k\u1ebft qu\u1ea3 x\xe9t nghi\u1ec7m); th\u1ef1c hi\u1ec7n khai b\xe1o y t\u1ebf tr\u01b0\u1edbc khi b\u1eaft \u0111\u1ea7u h\xe0nh tr\xecnh v\xe0 \u0111\u1ea3m b\u1ea3o xu\u1ea5t tr\xecnh \u0111\u01b0\u1ee3c m\xe3 QR k\u1ebft qu\u1ea3 khai b\xe1o y t\u1ebf (tr\xean \u0111i\u1ec7n tho\u1ea1i th\xf4ng minh ho\u1eb7c thi\u1ebft b\u1ecb ph\xf9 h\u1ee3p ho\u1eb7c b\u1ea3n gi\u1ea5y) khi c\xf3 y\xeau c\u1ea7u ki\u1ec3m tra. &nbsp;</p>\n    <p style='color: rgb(0, 0, 0); font-family: arial; font-size: 13px; word-spacing: -0.1px;'>\n        L\xe1i xe, ng\u01b0\u1eddi tr\xean ph\u01b0\u01a1ng ti\u1ec7n h\u1ea1n ch\u1ebf r\u1eddi kh\u1ecfi cabin c\u1ee7a xe; tr\u01b0\u1eddng h\u1ee3p r\u1eddi kh\u1ecfi cabin c\u1ee7a xe ph\u1ea3i th\u1ef1c hi\u1ec7n nghi\xeam \u201cnguy\xean t\u1eafc 5K\u201d; gi\u1eef kho\u1ea3ng c\xe1ch an to\xe0n t\u1ed1i thi\u1ebfu 2m khi ti\u1ebfp x\xfac v\u1edbi ng\u01b0\u1eddi kh\xe1c; th\u1ef1c hi\u1ec7n s\xe1t khu\u1ea9n c\xf3 tr\xean 60% n\u1ed3ng \u0111\u1ed9 c\u1ed3n ho\u1eb7c r\u1eeda tay th\u01b0\u1eddng xuy\xean b\u1eb1ng x\xe0 ph\xf2ng trong \xedt nh\u1ea5t 30 gi\xe2y; s\u1eed d\u1ee5ng c\xe1c ph\u01b0\u01a1ng ti\u1ec7n th\xf4ng tin li\xean l\u1ea1c, h\u1ea1n ch\u1ebf ti\u1ebfp x\xfac tr\u1ef1c ti\u1ebfp. \u0110\u01a1n v\u1ecb (ng\u01b0\u1eddi) giao nh\u1eadn h\xe0ng h\xf3a ch\u1ecbu tr\xe1ch nhi\u1ec7m v\u1ec1 x\u1ebfp d\u1ee1 h\xe0ng h\xf3a, kh\xf4ng ti\u1ebfp x\xfac v\u1edbi l\xe1i xe, ng\u01b0\u1eddi tr\xean ph\u01b0\u01a1ng ti\u1ec7n; ch\u1ee7 \u0111\u1ed9ng b\u1ed1 tr\xed \u0111\u1ecba \u0111i\u1ec3m, n\u01a1i ngh\u1ec9 ng\u01a1i, l\u01b0u tr\xfa t\u1ea1m th\u1eddi cho l\xe1i xe, ng\u01b0\u1eddi tr\xean ph\u01b0\u01a1ng ti\u1ec7n trong th\u1eddi gian ch\u1edd x\u1ebfp, d\u1ee1 h\xe0ng h\xf3a \u0111\u1ea3m b\u1ea3o c\xf4ng t\xe1c ph\xf2ng, ch\u1ed1ng d\u1ecbch theo quy \u0111\u1ecbnh.</p>\n    <p style='color: rgb(0, 0, 0); font-family: arial; font-size: 13px; word-spacing: -0.1px;'>\n        \u0110\u1ed1i v\u1edbi ho\u1ea1t \u0111\u1ed9ng v\u1eadn chuy\u1ec3n c\xf4ng nh\xe2n, chuy\xean gia, l\xe1i xe, c\xf4ng nh\xe2n, chuy\xean gia tr\xean xe th\u1ef1c hi\u1ec7n khai b\xe1o y t\u1ebf theo quy \u0111\u1ecbnh c\u1ee7a B\u1ed9 Y t\u1ebf v\xe0 theo y\xeau c\u1ea7u ph\xf2ng ch\u1ed1ng d\u1ecbch c\u1ee7a \u0111\u1ecba ph\u01b0\u01a1ng n\u01a1i \u0111i, n\u01a1i \u0111\u1ebfn; th\u1ef1c hi\u1ec7n khai b\xe1o y t\u1ebf tr\u01b0\u1edbc khi b\u1eaft \u0111\u1ea7u h\xe0nh tr\xecnh v\xe0 th\u1ef1c hi\u1ec7n nghi\xeam \u201cnguy\xean t\u1eafc 5K\u201d trong su\u1ed1t h\xe0nh tr\xecnh; mang theo b\u1ea3n ch\xednh Gi\u1ea5y x\xe9t nghi\u1ec7m \xe2m t\xednh SARSCoV-2 c\xf2n hi\u1ec7u l\u1ef1c; t\u1ef1 theo d\xf5i nhi\u1ec7t \u0111\u1ed9 v\xe0 s\u1ee9c kh\u1ecfe h\xe0ng ng\xe0y, n\u1ebfu c\xf3 s\u1ed1t, ho, kh\xf3 th\u1edf, m\u1ec7t m\xf5i th\xec th\xf4ng b\xe1o cho \u0111\u01a1n v\u1ecb qu\u1ea3n l\xfd v\xe0 c\u01a1 quan y t\u1ebf \u0111\u1ec3 x\u1eed l\xfd k\u1ecbp th\u1eddi.</p>\n    <p style='color: rgb(0, 0, 0); font-family: arial; font-size: 13px; word-spacing: -0.1px;'>\n        L\xe1i xe khi \u0111i\u1ec1u khi\u1ec3n ph\u01b0\u01a1ng ti\u1ec7n di chuy\u1ec3n tr\xean \u0111\u01b0\u1eddng th\u1ef1c hi\u1ec7n nghi\xeam nguy\xean t\u1eafc \u201c01 cung \u0111\u01b0\u1eddng, 02 \u0111\u1ecba \u0111i\u1ec3m\u201d, kh\xf4ng \u0111\u01b0\u1ee3c d\u1eebng, \u0111\u1ed7 d\u1ecdc \u0111\u01b0\u1eddng (tr\u1eeb tr\u01b0\u1eddng h\u1ee3p kh\u1ea9n c\u1ea5p); mang theo danh s\xe1ch v\xe0 ki\u1ec3m so\xe1t ch\u1eb7t ch\u1ebd ng\u01b0\u1eddi \u0111i xe.</p>\n    <p style='color: rgb(0, 0, 0); font-family: arial; font-size: 13px; word-spacing: -0.1px;'>\n        S\u1edf GT-VT c\u0169ng y\xeau c\u1ea7u c\xe1c doanh nghi\u1ec7p, c\u01a1 s\u1edf s\u1ea3n xu\u1ea5t kinh doanh th\u1ef1c hi\u1ec7n ph\u01b0\u01a1ng \xe1n \u201c3 t\u1ea1i ch\u1ed7\u201d, \u201c1 cung \u0111\u01b0\u1eddng, 2 \u0111\u1ecba \u0111i\u1ec3m\u201d ho\u1eb7c linh \u0111\u1ed9ng c\xf9ng l\xfac 2 ph\u01b0\u01a1ng \xe1n ph\u1ea3i ch\u1ecbu tr\xe1ch nhi\u1ec7m \u0111\u1ea3m b\u1ea3o th\u1ef1c hi\u1ec7n quy \u0111\u1ecbnh v\u1ec1 ph\xf2ng, ch\u1ed1ng d\u1ecbch Covid-19 t\u1ea1i c\xe1c \u0111\u1ecba \u0111i\u1ec3m t\u1eadp k\u1ebft ph\u01b0\u01a1ng ti\u1ec7n, trung chuy\u1ec3n, giao nh\u1eadn h\xe0ng h\xf3a trong ph\u1ea1m vi qu\u1ea3n l\xfd; ch\u1ecbu tr\xe1ch nhi\u1ec7m tr\u01b0\u1edbc ph\xe1p lu\u1eadt v\u1ec1 b\u1ea3o \u0111\u1ea3m to\xe0n b\u1ed9 c\xf4ng nh\xe2n, chuy\xean gia khi l\u01b0u th\xf4ng ph\u1ea3i tu\xe2n th\u1ee7 c\xe1c bi\u1ec7n ph\xe1p ph\xf2ng, ch\u1ed1ng d\u1ecbch theo quy \u0111\u1ecbnh; l\u1eadp danh s\xe1ch c\xf4ng nh\xe2n, chuy\xean gia; danh s\xe1ch ph\u01b0\u01a1ng ti\u1ec7n, l\xe1i xe, nh\xe2n vi\xean ph\u1ee5c v\u1ee5 (n\u1ebfu c\xf3) tham gia v\u1eadn chuy\u1ec3n.</p>\n    </span>"}];e.DataGioiThieu=n